.cabecera {
  width: 100%;
  background-color: #011126;
  font-family: Forum;
  font-weight: bolder;
  display: grid;
}

.cabecera p {
  color: #fff;
  width: -moz-fit-content;
  width: fit-content;
}

.cabecera img {
  width: 200px;
  width: 175px;
  margin-left: 5px;
}

.cabecera ul {
  flex-wrap: wrap;
  gap: 9px;
  display: flex;
}

.cabecera li {
  list-style: none;
}

.cabecera a {
  color: #fff;
  text-decoration: none;
}

.cabecera2 {
  width: 100%;
  background-color: #011126;
  border-top: 4px solid #a3c4d9;
  justify-content: center;
  font-family: Forum;
  display: flex;
}

.cabecera2 p {
  color: #fff;
}

.cabecera2 img {
  width: 80px;
  height: 80px;
}

@media (min-width: 320px) {
  .cabecera img {
    width: 100px;
    height: 100px;
  }

  .cabecera ul {
    margin-left: 2px;
  }
}

@media (min-width: 375px) {
  .cabecera ul {
    margin-right: 5px;
  }
}

@media (min-width: 425px) {
  .cabecera ul {
    margin-right: 20px;
  }
}

@media (min-width: 500px) {
  .cabecera {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (min-width: 1000px) {
  .cabecera {
    grid-template-columns: repeat(2, 1fr);
  }

  .cabecera ul {
    margin-top: 40px;
  }
}

@keyframes animate {
  0% {
    background-image: url("slider1.fd567d7c.png");
