}

body {
  margin: 0;
}

html {
  font-size: 22px;
}

p {
  text-align: center;
}

.slider {
  height: 454px;
  width: 100%;
  background-size: cover;
  border-radius: 10px;
  animation: 20s ease-in-out infinite animate;
}

.container2 {
  max-width: 100%;
  grid-gap: 1rem;
  margin: 0 auto;
  display: grid;
}

.flip-card {
  width: 300px;
  height: 365px;
  perspective: 1000px;
  background-color: #0000;
  font-family: Forum;
}

.flip-card img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
}

.flip-card a {
  color: #fff;
  text-decoration: none;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-inner {
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  transition: transform .8s;
  position: relative;
}

.flip-card-front {
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  display: flex;
  position: absolute;
  box-shadow: 0 8px 14px #011126;
}

.flip-card-back {
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  color: #fff;
  background: linear-gradient(120deg, #010505 30%, #011126 88%, #fff 78%);
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  display: flex;
  position: absolute;
  transform: rotateY(180deg);
  box-shadow: 0 8px 14px #011126;
}

.primeras_fichas, .segundas_fichas {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 120px;
  display: flex;
}

footer {
  color: #fff;
  width: 100%;
  background-color: #011126;
  justify-content: space-around;
  gap: 20px;
  margin-top: 10px;
  display: flex;
}

